.talk-list {
  margin: 15px 15px 0 50px;
}
.talk-list__item {
	position: relative;
  background: rgba(255,255,255,.1);
  border-radius: 6px;
  padding: .5em;
  font-size: 0.8rem;
  color: #dddddd;
  margin-bottom: 1em;
  width: 20em;
}
.talk-list__item:after {
	right: 100%;
	top: 50%;
	border: solid transparent;
	content: " ";
	height: 0;
	width: 0;
	position: absolute;
	pointer-events: none;
	border-color: rgba(255,255,255,.0);
	border-right-color: rgba(255,255,255,.1);
	border-width: 8px;
	margin-top: -8px;
}



















